import React, { useState, useEffect } from "react";
import { Col, Container, Row } from "react-bootstrap";
import "./Weather.css";

import axios from "axios";
function Weather() {
  const [error, setError] = useState(null);
  const [weatherData, setWeatherData] = useState(null);
  const [city, setCity] = useState("solapur");
  const searchWheather = () => {
    setError(null);
    const url = `http://api.openweathermap.org/data/2.5/weather?q=${city}&units=metric&APPID=4aece0749a2e8bc731b2448e08024f64`;

    axios
      .get(url)
      .then(function (response) {
        // handle success
        if (response.data) {
          // console.log(response.data);
          setWeatherData({
            city: response.data.name,
            temp: response.data.main.temp,
            minTemp: response.data.main.temp_min,
            maxTemp: response.data.main.temp_max,
            lat: response.data.coord.lat,
            lon: response.data.coord.lon,
            main: response.data.weather[0].main,
            description: response.data.weather[0].description,
          });
        }
      })
      .catch(function (error) {
        // handle error
        setError(error.response.data.message);
        // console.log(error.response.data.message);
      });
  };
  useEffect(() => {
    searchWheather();
    return () => {
      searchWheather();
    };
  }, [city, setCity]);
  return (
    <>
      <Container>
        <Row className="d-flex justify-content-center">
          <Col md={4} className="wheather-card p-3 mt-3 border">
            <input
              type="text"
              className="form-control"
              placeholder="Search city"
              onChange={(event) => setCity(event.target.value)}
              value={city}
            />
            {error ? (
              <h1 className="text-center text-danger py-3">{error}</h1>
            ) : (
              <div>
                <div className="d-flex justify-content-between my-3">
                  <span>
                    Lat:{" "}
                    <span className="text-warning">
                      {weatherData && weatherData.lat}
                    </span>
                  </span>
                  <span>
                    Lon:{" "}
                    <span className="text-warning">
                      {weatherData && weatherData.lon}
                    </span>
                  </span>
                </div>
                <hr />
                <h1 className="text-center">
                  {weatherData && weatherData.temp} <sup>o</sup>C
                </h1>
                <div className="d-flex justify-content-between my-3">
                  <span>
                    Min temp:{" "}
                    <span className="text-warning">
                      {weatherData && weatherData.minTemp}{" "}
                    </span>
                  </span>
                  <span>
                    Max temp:{" "}
                    <span className="text-warning">
                      {weatherData && weatherData.maxTemp}{" "}
                    </span>
                  </span>
                </div>
                <hr />
                <h4 className="text-center text-success">
                  {weatherData && weatherData.city}
                </h4>
                <hr />
                <h6>{weatherData && weatherData.main}</h6>
                <p>{weatherData && weatherData.description}</p>
              </div>
            )}
          </Col>
        </Row>
      </Container>
    </>
  );
}

export default Weather;
