import React, { useState } from "react";
import { Button, Container } from "react-bootstrap";
import "../../styles/LuckySpin.css";
function LuckySpin() {
  const sufflePlaceholderImage = "./images/shuffle.gif";
  const [wallet, setWallet] = useState(50);
  const [bgColors, setBgColors] = useState({
    first: "red",
    second: "blue",
    third: "green",
  });
  const [investmentValue, setInvestmentValue] = useState(10);
  const [userWin, setUserWin] = useState(null);
  const [imagesOptions, setImagesOptions] = useState({
    imageOne: "./images/rock.png",
    imageTwo: "./images/paper.png",
    imageThree: "./images/scissor.png",
  });
  const images = [
    "./images/rock.png",
    "./images/paper.png",
    "./images/scissor.png",
  ];
  const checkForResult = () => {
    let img1 = images[Math.floor(Math.random() * images.length)];
    let img2 = images[Math.floor(Math.random() * images.length)];
    let img3 = images[Math.floor(Math.random() * images.length)];
    if ((img1 === img2) === img3) {
      setBgColors({
        first: "green",
        second: "green",
        third: "green",
      });
      setUserWin("winner");
      setWallet(Math.floor(wallet + investmentValue * 2));
    } else if (img1 === img2) {
      setBgColors({
        first: "green",
        second: "green",
        third: "red",
      });
      setUserWin("half-win");
      setWallet(Math.floor(wallet - investmentValue + investmentValue / 2));
    } else if (img1 === img3) {
      setBgColors({
        first: "green",
        second: "red",
        third: "green",
      });
      setUserWin("half-win");
      setWallet(Math.floor(wallet - investmentValue + investmentValue / 2));
    } else if (img2 === img3) {
      setBgColors({
        first: "red",
        second: "green",
        third: "green",
      });
      setUserWin("half-win");
      setWallet(Math.floor(wallet - investmentValue + investmentValue / 2));
    } else {
      setBgColors({
        first: "red",
        second: "blue",
        third: "green",
      });
      setUserWin("loose");
    }
    setImagesOptions({
      imageOne: img1,
      imageTwo: img2,
      imageThree: img3,
    });
  };
  const handleSpin = () => {
    if (wallet - investmentValue < 0 || investmentValue <= 0) {
      return;
    }
    setWallet(wallet - investmentValue);

    setImagesOptions({
      imageOne: sufflePlaceholderImage,
      imageTwo: sufflePlaceholderImage,
      imageThree: sufflePlaceholderImage,
    });
    setTimeout(function () {
      checkForResult();
    }, 3000);
  };

  return (
    <>
      <Container>
        <div className="lucky-spin-container">
          <div className="spin-container">
            <div className="score-board">
              <span>Wallet: {wallet}</span>
              <span>
                Invest:{" "}
                <input
                  type="number"
                  value={investmentValue}
                  onChange={(event) =>
                    setInvestmentValue(event.currentTarget.value)
                  }
                  style={{ width: "70px" }}
                />
              </span>
            </div>
            <div className="lucky-spin-box">
              <div
                className="box box-1"
                style={{ backgroundColor: bgColors.first }}
              >
                <img
                  src={imagesOptions.imageOne}
                  // src={sufflePlaceholderImage}
                  alt="spin image 1"
                  style={{ width: "100px", height: "100px" }}
                />
              </div>
              <div
                className="box box-2"
                style={{ backgroundColor: bgColors.second }}
              >
                <img
                  src={imagesOptions.imageTwo}
                  alt="spin image 2"
                  style={{ width: "100px", height: "100px" }}
                />
              </div>
              <div
                className="box box-3"
                style={{ backgroundColor: bgColors.third }}
              >
                <img
                  src={imagesOptions.imageThree}
                  alt="spin image 3"
                  style={{
                    width: "100px",
                    height: "100px",
                  }}
                />
              </div>
            </div>
            <div className="play-button">
              <Button
                variant={"success"}
                className="w-100"
                onClick={handleSpin}
              >
                Start
              </Button>
            </div>
            <h1 className="text-center p-3">
              {userWin === "win" && <h1 className="text-success">You win</h1>}
              {userWin === "half-win" && (
                <h1 className="text-success">
                  You win half of <br />
                  the price invested!
                </h1>
              )}
              {userWin === "loose" && (
                <h1 className="text-danger">You Loose!</h1>
              )}
            </h1>
          </div>
        </div>
      </Container>
    </>
  );
}

export default LuckySpin;
